import utils from '../../../common/js/util.js'
export default {
	data() {
		return {
			name:'',//姓名
			idCardCode:'',//身份证号码
			accountNo:'',//银行卡号
			code:'',//短信验证码
			bank:'',//开户行
			mobile:'',//手机号
			disabled: false,	// 禁止点击
			nameDisabled: false,
			type:'',
			state:'',
			bank_account_name:'',
		};
	},
	onLoad(e) {
		this.$store.dispatch('getCxInfo',{}).then(res=>{
			this.name = res.data.uinfo.realname
			this.idCardCode = res.data.uinfo.idcards
			this.mobile = res.data.uinfo.mobile
			this.nameDisabled = true
		}).catch(err=>{})
	},
	methods:{	
		submit(){
			if(this.name == ''){
				uni.showToast({
					title: '请输入姓名',
					icon: 'none', //loading   none  success
					mask: false,
					duration: 1500
				});
				return false;
			}
			if(this.identity == ''){
				uni.showToast({
					title: '请输入身份证号码',
					icon: 'none', //loading   none  success
					mask: false,
					duration: 1500
				});
				return false;
			}
			if(this.accountNo == ''){
				uni.showToast({
					title: '请输入银行卡号',
					icon: 'none', //loading   none  success
					mask: false,
					duration: 1500
				});
				return false;
			}
			if(this.mobile == ''){
				uni.showToast({
					title: '请输入银行预留手机号',
					icon: 'none', //loading   none  success
					mask: false,
					duration: 1500
				});
				return false;
			}
			let data = {
				merchant_name:this.name,
				channel_type: 'withdraw',
				bank_cardno: this.accountNo,
				bank_abbr:this.bank,
				bank_mobile:this.mobile,
				id_card_no: this.idCardCode,
				bank_name: this.bank_account_name,
				token: uni.getStorageSync('token')
			}
			this.disabled = true
			this.$store.dispatch('setCxInfo',data)
			.then(result=>{
				uni.showToast({
					title:'绑定成功',
					icon: 'none', //loading   none  success
					mask: false,
					duration: 1500,
					success: (res)=>{
						setTimeout(function(){
							this.disabled = false
							uni.navigateBack({
								delta:1
							})
						},1500)
					}
				})
			}).catch(err=>{
				this.disabled = false
			})		
		},
		input(){
			this.$store.dispatch('getBankInfo',{
				bank_cardno: this.accountNo
			}).then(res=>{
				this.bank_account_name = res.data.bank_name
				this.bank = res.data.bank_abbr
			}).catch(err=>{})
		},
	}
}
